.wrapper_profile_friends {
  background-color: rgb(51, 51, 54);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  box-shadow: none;
  color: rgb(255, 255, 255);
  direction: ltr;
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 95px;
  line-height: 15.002px;
  margin-bottom: 0px;
  margin-left: 0px;
  margin-right: 0px;
  margin-top: 15px;
  position: relative;
  text-align: left;
}
.top_friends_info {
  display: flex;
  justify-content: space-between;
  padding: 12px 14px;
}
.top_friends_info span {
  cursor: pointer;
}

.count_friends {
  color: rgb(147, 147, 147);
}

.friend_label {
  margin-right: 10px;
}
.updates_friends span:hover {
  border-bottom: 1px solid #ffffff;
}
.friends_main {
  padding: 5px 3px;
}
.info_about_friend{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.info_about_friend {

}
.friends_main_1stolb {
  display: flex;
  margin-bottom: 13px;
  justify-content: center;
}
.friends_main_2stolb {
  display: flex;
  justify-content: center;
}
.middle_friend {
  margin: 0 19px;
}
.info_about_friend{
    cursor: pointer;
}
.friends_main img {
  border-bottom-color: rgba(0, 0, 0, 0);
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  border-bottom-style: none;
  border-bottom-width: 0px;
  border-image-outset: 0;
  border-image-repeat: stretch;
  border-image-slice: 100%;
  border-image-source: none;
  margin-bottom: 10px;
  border-image-width: 1;
  border-left-color: rgba(0, 0, 0, 0);
  border-left-style: none;
  border-left-width: 0px;
  border-right-color: rgba(0, 0, 0, 0);
  border-right-style: none;
  border-right-width: 0px;
  border-top-color: rgba(0, 0, 0, 0);
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  border-top-style: none;
  border-top-width: 0px;
  color: rgba(0, 0, 0, 0);
  cursor: pointer;
  direction: ltr;
  display: inline;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 12.5px;
  font-weight: 400;
  height: 50px;
  line-height: 14.425px;
  overflow-x: hidden;
  overflow-y: hidden;
  text-align: center;
  width: 50px;
}
